	export default {
		data() {
			return {
				title: 'picker',
				 array: ['装修', '建材','家具','家纺'],
				 index: 0,
				 imgroup : [],
				 types:"装修",
				 jing:null,
				 wei:null,
				 dz:"",
				 shopname:"",
				 introduce:"",
				 adress:"",
				 img:""
			}
		},
		onLoad(img) {
		   this.img=img.image
		},
		methods: {
			map:function(){
				var that = this;
				
				uni.chooseLocation({
				    success: function (res) {
						
						var tmp = res;
				        console.log('位置名称：' + res.name);
				        console.log('详细地址：' + res.address);
				        console.log('纬度：' + res.latitude);
				        console.log('经度：' + res.longitude);		
						if(res.address!=''){
							that.dz=res.address;
							that.jing=res.longitude;
							that.wei=res.latitude;
						}else{
							uni.showToast({
								title: '请选择地点',
								icon : "none",
							});
						}
				    }
				});
			},
			del:function(i){
				this.imgroup.splice(i,1);
			},
			back : function(){
			   uni.navigateBack({
			  
			   });
			},
			sq:function(){
				
				if(this.jing!=null&&this.wei!=null&&this.dz!=""&&this.shopname!=""&&this.introduce!=""&&this.adress!=""){
					let that=this;
					let usurl=that.url1+"/dinghai/shop/addshop";
					uni.showLoading({
						title:"正在开通",mask: true
					})
					uni.request({
							url: usurl,
							header:{
								"Content-Type" : "application/x-www-form-urlencoded"
							},
							method:'POST',
							data: {
								shopname:that.shopname,
								adress:that.dz+that.adress,
								introduce:that.introduce,
								uid:that.user.uid,
								type:that.types,
								Longitude:that.jing,
								Latitude:that.wei,
								shopImg:that.img
							},
					    success: (res) => {
							
								console.log(res)
							if(res.statusCode==200){
								// console.log(res)
								if(res.data.code==200){
									uni.showToast({
										title:"开通成功",
										icon:"none"
									})
									uni.navigateBack({
										delta:2
									})
								}else{
									uni.showToast({
										title:"开通失败",
										icon:"none"
									})
								}
							}else{
								uni.showToast({
									title:"您的网络打了个盹",
									icon:"none"
								})
							}
					    },
						fail : function(err){
							uni.showToast({
								title:"您的网络打了个盹",
								icon:"none"
							})
						}
					});
				}else{
					uni.showToast({
						icon:"none",
						title:"请完整填写"
					})
				}
			},
			getDate(type) {
			            const date = new Date();
			            let year = date.getFullYear();
			            let month = date.getMonth() + 1;
			            let day = date.getDate();
			
			            if (type === 'start') {
			                year = year - 60;
			            } else if (type === 'end') {
			                year = year + 2;
			            }
			            month = month > 9 ? month : '0' + month;;
			            day = day > 9 ? day : '0' + day;
			            return `${year}-${month}-${day}`;
			        },
			bindPickerChange: function(e) {
			            this.index = e.target.value;
					    this.types = this.array[this.index];
			        },
					
					
					  // 选择图片,最多6张
					sel_img : function(){
						var that = this;
						console.log(typeof that.imgroup[0])
						if(this.imgroup.length>=6){
							uni.showToast({
								title:"最多上传6张图",
								icon:"none"
							})
							
						}else{
							uni.chooseImage({
							   count: 1, //默认9
							   sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
							   sourceType: ['album'], //从相册选择
							   success: function (res) {
								   console.log(res.tempFilePaths,res);
								   let thatRes=res;
								   if(/(.jpg|.JPG|.jpeg|.JPEG|.PNG|.png)$/.exec(res.tempFilePaths[0])){
									   uni.getFileSystemManager().readFile({
									       filePath: res.tempFilePaths[0], //选择图片返回的相对路径
									       encoding: 'base64', //编码格式
									       success: res => { //成功的回调
										   let KuoZhan=thatRes.tempFilePaths[0].split(".")
										   console.log(KuoZhan[KuoZhan.length-1])
									           let base64 = 'data:image/'+KuoZhan[KuoZhan.length-1]+';base64,' + res.data //不加上这串字符，在页面无法显示的哦
									   			that.imgroup.push(base64);
									       }
									   })
								   }else{
									   uni.showToast({
									   	icon:"none",
										title:"上传格式错误！请上传jpg，jpeg，png格式的图片"
									   })
								   }
							   }
							});
						}
					},
		 }
}
